.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #1f1f1f;
    height: 120px;
    padding: 16px 100px;
  }
  
  .navbar img {
    height: 40px;
    object-fit: contain;
    
  }
  .nav-items{
    color: white;
    display: flex;
    font-size: 16px;
    font-family: mo;
    gap: 16px;
    justify-content: center;
    list-style: none;
  }
  